import React from 'react';
import { RightOutlined, ReloadOutlined } from '@ant-design/icons';

import { StyledButton } from './../Styled';
import './style.scss';

const ListItem = (props) => {
  const { title, subTitle, action, loading, icon, noBorder, textClasses, boxClasses } = props;

  const iconProps = {
    className: 'desc-color',
    style: { fontSize: 20 },
    spin: loading || false
  };

  return (
    <div className={`border-border setting-row ${noBorder ? 'noBorder' : ''} ${boxClasses || 'p-v-20'}`}>
      <StyledButton
        className="flex-container space flex-center text-left full-width p-a-0"
        onClick={action ? action : () => {}}
        disabled={loading || false}
      >
        <div className="flex-1">
          <h4 className={`header-text bold body-color ${textClasses || ''}`}>
            {title}
          </h4>
          {subTitle && <h4 className={`body-text normal desc-color ${textClasses || ''}`}>
            {subTitle}
          </h4>}
        </div>
        <div className="flex-auto">
          {icon ? icon : (typeof (loading) !== 'undefined' ? 
            <ReloadOutlined { ...iconProps }  /> :
            <RightOutlined { ...iconProps } />
          )}
        </div>
      </StyledButton>
    </div>
  );
};

export default ListItem;