import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import moment from 'moment';

import helpers from './../../Utils/helpers';
import Item from './../../Components/ListItem';
import Header from './../../Components/Structure/header';
import { updateAction } from './../../Reducers/Basic/actions';

const Settings = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const updatedAt = useSelector(state => state?.basic?.updatedAt || '');

  const handleUpdate = async () => {
    setLoading(true);
    dispatch(updateAction());
    await helpers.sleep(2800)
    setLoading(false);
  };

  return (
    <div className="flex-1 flex-container col relative">
      <Header title={t('settings.header')} />
      <div className="flex-1 p-v-5 p-h-20 scroll-view frame">
        <Item
          loading={loading}
          title={t('settings.contents.update')}
          subTitle={t('lastest', { updatedAt: moment(updatedAt).format('YYYY-MM-DD hh:mm:ss') })}
          action={handleUpdate}
        />
        <Item
          title={t('settings.contents.languages')}
          action={() => document.toggleDrawer('languages')}
        />
        <Item
          title={t('settings.contents.records')}
          action={() => document.toggleDrawer('records')}
        />
        <Item
          title={t('settings.contents.statement')}
          action={() => document.toggleDrawer('statement')}
        />
        <Item
          title={t('settings.contents.privacy')}
          action={() => document.toggleDrawer('privacy')}
        />
        <Item
          title={t('settings.contents.terms')}
          action={() => document.toggleDrawer('terms')}
        />
        <Item
          title={t('settings.contents.welcome')}
          action={() => {
            localStorage.removeItem('onBoarded');
            history.replace(`/?s=${uuidv4()}`)
          }}
        />
        <Item
          noBorder
          title={(
            <>
              {t('settings.contents.about')}
              &nbsp;
              <span className="primary-color underline">
                {t('name')}
              </span>
            </>
          )}
          action={() => window.open('https://www.leavehomesafe.gov.hk/')}
          icon={(<div className="header-text normal">
            {t('version', { versionCode: '1.1.5' })}
          </div>)}
        />
      </div>
    </div>
  );
};

export default Settings;
