import moment from 'moment';
import { SET_RECORDS_REDUCER, PUSH_RECORDS_REDUCER } from './../type';

export const loadLocalStorageRecordsAction = () => {
  let { records } = localStorage;
  records = records ? JSON.parse(records) : [];

  return ({
    target: '',
    type: SET_RECORDS_REDUCER,
    data: records
  });
};


export const saveRecordAction = (record) => {
  let { records } = localStorage;
  records = records ? JSON.parse(records) : [];
  const obj = {
    ...record,
    done: false,
    leftAt: null,
    startedAt: moment().format('YYYY-MM-DD HH:mm')
  };
  records.push(obj);
  localStorage.setItem('records', JSON.stringify(records));

  return ({
    target: '',
    type: PUSH_RECORDS_REDUCER,
    data: obj
  });
};

export const updateRecordAction = (hours = null, done = false) => {
  let { records } = localStorage;
  records = records ? JSON.parse(records) : [];

  records = records.map(record => {
    if (!record.leftAt || !record.done) {
      const obj =  { done };
      if (hours) {
        switch (typeof (hours)) {
          case 'number':
            obj.leftAt = moment(record.startedAt).add(hours, 'hours').format('YYYY-MM-DD HH:mm');
            break;
          case 'string':
            obj.leftAt = hours;
            break;
          default:
            break;      
        }
      }
      record = { ...record, ...obj };
    }
    return record;
  });

  localStorage.setItem('records', JSON.stringify(records));

  return ({
    target: '',
    type: SET_RECORDS_REDUCER,
    data: records
  });
};
