import React, { Fragment, useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import InputMaterialUi from 'input-material-ui';
import QrReader from 'react-qr-reader'
import { Drawer } from 'antd';
import { has, cloneDeep, isNil, find, reverse } from 'lodash';
import moment from 'moment';
import {
  WarningOutlined,
  CheckOutlined,
  LeftOutlined,
  LockFilled,
} from '@ant-design/icons';

import helpers from './../../Utils/helpers';
import Header from './header';
import BottomTab from './bottomTab';
import ListItem from './../ListItem';
import TrackModal from './../TrackModal';
import Dragger from './../Dragger';
import { StyledCheckbox, StyledButton } from './../../Components/Styled';
import { loadLocalStorageBasicAction } from './../../Reducers/Basic/actions';
import {
  loadLocalStorageRecordsAction,
  updateRecordAction,
  saveRecordAction
} from './../../Reducers/Records/actions';

const defaultForm = { id: '', name: '', phone: '', agree: false };

let timeout;

const Structure = ({ children }) => {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  const location = useLocation();
  const { pathname } = location;

  const [type, setType] = useState(null);
  const [car, setCar] = useState('');
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState(cloneDeep(defaultForm));

  const [showModal, setShowModal] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);

  const [isDrag, setIsDrag] = useState(false);

  const records = useSelector(state => state?.records || []);
  const record = find(records, { done: false });

  if (!timeout && record?.leftAt) {
    console.log('timeout', moment(record.leftAt).diff(moment()));
    timeout = setTimeout(
      () => dispatch(updateRecordAction(moment().format('YYYY-MM-DD HH:mm'), true)),
      moment(record.leftAt).diff(moment()),
      dispatch,
      moment,
      updateRecordAction
    );
  }

  useEffect(() => {
    document.toggleModal = (d) => {
      setShowModal(v => d !== null ? d : !v);
    };
    document.toggleDrawer = (t = null) => {
      if (t) setType(t)
      else setTimeout(() => setType(t), 300, setType, t);
      setShowDrawer(v => !v)
    };
    
    dispatch(loadLocalStorageBasicAction());
    dispatch(loadLocalStorageRecordsAction());
  }, []);

  const handleQrCode = (str) => {
    const place = helpers.deccode(str);
    if (place) {
      dispatch(saveRecordAction({ type: 'stay', location: place }));
      setShowModal(true);
      document.toggleDrawer();
    }
  }

  const handleFormChange = (key, val) => {
    setForm(f => ({ ...f, [key]: val }));
  };

  const handleFormSubmit = async () => {
    setLoading(true);
    await helpers.sleep(3800);
    setForm(cloneDeep(defaultForm));
    setLoading(false);
    document.toggleDrawer();
  };

  const handleCarDetect = () => {
    const carRef = document.getElementById('carRef');
    if (carRef) carRef.focus();
  };

  const handleCarSubmit = async () => {
    setLoading(true);
    dispatch(saveRecordAction({ type: 'transport', location: car }));
    await helpers.sleep(2800);
    setLoading(false);
    setCar('');
    setShowModal(true);
    document.toggleDrawer();
  };

  const handleLeave = () => {
    dispatch(updateRecordAction(moment().format('YYYY-MM-DD HH:mm'), true));
    setShowModal(false);
    clearTimeout(timeout);
  };

  const renderTimeRange = (start, leave) => {
    return start && leave ? `${start} - ${leave}` : null;
  };

  const renderTitle = () => {
    if (has(t('settings.contents', { returnObjects: true }), type))
      return t(`settings.contents.${type}`);
    else if (has(t('home.drawer', { returnObjects: true }), type))
      return t(`home.drawer.${type}.header`);
    else
      return '';
  };

  const renderContent = () => {
    const classes = {
      terms: {
        opening: 'text-center bold m-b-20',
        header: 'underline bold',
        title: 'header-text bold m-t-20 m-b-10',
        content: 'body-text normal m-b-10'
      },
      statement: {
        opening: 'none',
        header: 'text-left bold m-b-20',
        title: 'header-text normal underline m-t-20 m-b-10',
        content: 'body-text normal m-b-10'
      },
      privacy: {
        opening: 'none',
        header: 'text-left bold m-b-20',
        title: 'header-text bold underline m-t-20 m-b-10',
        content: 'body-text normal m-b-10'
      }
    }

    switch (type) {
      case 'terms':
      case 'statement':
      case 'privacy':
        return (
          <Fragment>
            <h2 className={classes[type].opening}>
              {t(`${type}.title`)}
            </h2>
            <h2 className={classes[type].header}>
              {t(`settings.contents.${type}`)}
            </h2>
            {t(`${type}.contents`, { returnObjects: true }).map((content, i) => (
              <Fragment key={`content_${i}`}>
                <h2 className={classes[type].title}>
                  {content.title}
                </h2>
                {content.description.map((desc) => (
                  <h4 key={desc} className={classes[type].content} dangerouslySetInnerHTML={{ __html: desc }} />
                ))}
              </Fragment>
            ))}
          </Fragment>
        );
      case 'records':
        return (
          <Fragment>
            {reverse(records).filter(record => !isNil(record?.leftAt) && record?.done)
              .map((record, i) => (<ListItem
                noBorder
                icon={<span />}
                boxClasses={`${i === 0 ? 'p-b-10' : 'p-v-10'}`}
                textClasses="desc-text"
                key={`record_${i}`}
                title={t(`records.${record.type}`, { location: record.location })}
                subTitle={renderTimeRange(record.startedAt, record.leftAt)}
              />)
            )}
          </Fragment>
        );
      case 'stay':
        
        return (
          <div className="full-height qr-code-scanner relative">
            <QrReader
              delay={300}
              onError={console.log}
              onScan={handleQrCode}
              style={{ width: '100%' }}
            />
            <div
              className="absolute top-0 full-width"
              style={{
                background: `url(${require('./../../Assets/images/filter.png')}) center center no-repeat`,
                backgroundSize: 'cover',
                height: 'inherit'
              }}
            />
            <div
              className="absolute text-center blank-color body-text text-shadow full-width"
              style={{ bottom: '20vh' }}
            >
              {t('home.drawer.stay.header')}
            </div>
          </div>
        );
      case 'transport':
        return (
          <div
            className="flex-container col full-height"
            style={{ minHeight: 'calc(100vh - 120px)' }}
          >
            <div className="flex-1">
              <h2 className="title-text text-center bold m-b-20">
                {t('transport.title')}
              </h2>
              <h4 className="subTitle-text text-center normal m-b-20">
                {t('transport.content')}
              </h4>
              <InputMaterialUi
                id="carRef"
                InputProps={{
                  disabled: loading,
                  className: 'text-center letter-spacing-10 bold title-text body-color'
                }}
                value={car}
                onChange={(v) => setCar(v.toUpperCase())}
              />
              <div className="m-t-30">
                <StyledButton
                  type="primary"
                  className="full-width"
                  loading={loading}
                  disabled={car === ''}
                  onClick={handleCarSubmit}
                >
                  {t('common.button.confirm')}
                </StyledButton>
              </div>
            </div>
            <div>
              <StyledButton
                type="secondary"
                disabled={loading}
                className="full-width text-shadow"
                onClick={handleCarDetect}
              >
                {t('common.button.ocr-car')}
              </StyledButton>
            </div>
          </div>
        );
      case 'report':
        return (
          <Fragment>
            <h4 className="subTitle-text bold m-b-20">
              {t('report.content')}
            </h4>
            <div>
              {t('report.form', { returnObjects: true }).map((item, i) => (
                <div key={`report_input_${i}`} className={i >= 2 ? 'm-b-60' : 'm-b-30'}>
                  <InputMaterialUi
                    label={item.label}
                    InputProps={{
                      disabled: loading
                    }}
                    value={form[item.key]}
                    onChange={(v) => handleFormChange(item.key, v)}
                  />
                </div>
              ))}
              <div className="m-b-30 flex-container flex-center">
                <div className="flex-auto m-r-15">
                  <StyledCheckbox
                    disabled={loading}
                    checked={form.agree}
                    onChange={(e) => handleFormChange('agree', e.target.checked)}
                  />
                </div>
                <div className="flex-1 flex-center">
                  <strong dangerouslySetInnerHTML={{ __html: t('report.agree') }} />
                </div>
              </div>
              <StyledButton
                type="primary"
                className="full-width"
                loading={loading}
                disabled={form.id === '' || form.name === '' || form.phone === '' || !form.agree}
                onClick={handleFormSubmit}
              >
                {t('common.button.agree')}
              </StyledButton>
            </div>
          </Fragment>
        );
      case 'languages':
        return t('languages.contents', { returnObjects: true }).map((content, i) => (
          <ListItem
            key={content.code}
            title={content.name}
            boxClasses={`m-a-5 ${i === 0 ? 'p-b-15' : 'p-v-15'}`}
            textClasses={i === 0 ? 'disable-color' : ''}
            icon={t('language') === content.code ? (
              <CheckOutlined className="primary-color" style={{ fontSize: 20 }} />
            ) : (content.code === 'en-gb' ? 
              <WarningOutlined className="disable-color" style={{ fontSize: 20 }} /> :
            <span />)}
            action={content.code !== 'en-gb' ? () => i18n.changeLanguage(content.code) : () => {}}
          />
        ))
      default:
        return null;
    }
  };

  const renderFooter = () => {
    switch (type) {
      case 'records':
        return (
          <div className="border-background p-t-20 p-b-25 text-center">
            <LockFilled className="body-color body-text" />
            <div className="desc-text body-color">
              {t(`${type}.footer`)}
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <>
      <div className="container flex-container col structure card-shadow relative">
        <div className={`scroll-view flex-1 flex-container col ${pathname !== '/' ? 'p-b-115' : ''}`}>
          {children}
        </div>
        {pathname !== '/' && (
          <div className="absolute full-width blank-background bottom-0">
            <BottomTab />
          </div>
        )}
      </div>
      <Drawer
        className={`app-drawer ${type}`}
        headerStyle={{ padding: 0 }}
        title={(<Header
          title={renderTitle()}
          left={<LeftOutlined className="blank-color shadow" onClick={() => document.toggleDrawer()} />}
        />)}
        footer={renderFooter()}
        closable={false}
        visible={showDrawer}
      >
        {renderContent()}
      </Drawer>
      <TrackModal
        centered
        title=" "
        footer={null}
        visible={showModal}
        wrapClassName="app-modal"
        closeIcon={(<span className="title-text bold blank-color"></span>)}
        onCancel={() => setShowModal(false)}
      />
      <Dragger
        record={record}
        isDrag={isDrag}
        setIsDrag={setIsDrag}
        onLeave={handleLeave}
      />
    </>
  );
};

export default Structure;
