import { isEmpty, trim } from 'ramda';
const helpers = {};

helpers.sleep = (ms) => {
  return new Promise(resolve => setTimeout(resolve, ms));
};

helpers.deccode = (input) => {
  if (!(input || '').includes('HKEN:')) return null;
  const json = JSON.parse(window.atob(input.substring(14)));

  const trimmedZhName = json.nameZh ? trim(json.nameZh) : "";
  const name = !isEmpty(trimmedZhName) ? trimmedZhName : json.nameEn;

  return decodeURIComponent(escape(name));
}

export default helpers;