import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory, useLocation } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';

import helpers from './../../Utils/helpers';
import Header from './../../Components/Structure/header';
import { StyledCheckbox, StyledButton } from './../../Components/Styled';
import './style.scss';

const OnBoarding = () => {
  const { t } = useTranslation();
  const history = useHistory();
  const location = useLocation();
  const { search } = location;

  const [page, setPage] = useState(0);
  const [agree, setAgree] = useState(false);
  const [swiper, setSwiper] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const onMount = async () => {
      if (swiper) {
        if (search !== '' && page === 0) {
          setAgree(true);
          await helpers.sleep(100);
          handleNext();
          await helpers.sleep(250);
        }
        setLoading(false);
      }
    };
    onMount();
  }, [swiper]);

  useEffect(() => {
    if (page > 0) {
      document
        .querySelector('meta[name="apple-mobile-web-app-status-bar-style"]')
        .setAttribute('content', 'default');
    }
  }, [page]);

  const handlePrev = () => {
    if (swiper?.slidePrev) swiper.slidePrev();
  };

  const handleNext = () => {
    if (swiper.slideNext) {
      if (page < 4) {
        swiper.slideNext();
      } else {
        localStorage.setItem('onBoarded', true);
        document
          .querySelector('meta[name="apple-mobile-web-app-status-bar-style"]')
          .setAttribute('content', 'black-translucent');
        history.replace(search === '' ? '/home' : '/settings');
      }
    }
  };

  const renderContent = (screen) => {
    switch (screen) {
      case 0: 
        return (
          <>
            <h2 className="title-text bold body-color m-b-20">
              {t(`onBoarding.${screen}.title`)}
            </h2>
            <h4 className="body-text normal m-b-60">
              {t(`onBoarding.${screen}.description`)}
            </h4>
            <div className="m-b-30 flex-container flex-center">
              <div className="flex-auto m-r-15">
                <StyledCheckbox checked={agree} onChange={(e) => setAgree(e.target.checked)} />
              </div>
              <div className="flex-1 flex-center">
                <strong>
                  {t(`onBoarding.${screen}.agreement`)}
                  <span className="underline pointer" onClick={() => document.toggleDrawer('terms')}>
                    {t(`onBoarding.${screen}.terms`)}
                  </span>
                </strong>
              </div>
            </div>
            <StyledButton className="full-width" type="primary" disabled={!agree} onClick={handleNext}>
              {t('common.button.agree')}
            </StyledButton>
          </>
        );
      case 1:
      case 2:
      case 3:
      case 4: 
        return (
          <div className="flex-container col flex-1">
            <div className="text-center onBoarding-icon-container">
              <img
                className="onBoarding-icon"
                alt={`on boarding icon ${screen}`}
                src={require(`./../../Assets/images/onBoarding/${screen}.jpg`)}
              />
            </div>
            <div className="flex-1 text-center">
              <h2 className="title-text body-color m-b-20">
                {t(`onBoarding.${screen}.title`)}
              </h2>
              <h4 className="body-text onBoarding-content normal">
                {t(`onBoarding.${screen}.description`)}
              </h4>
            </div>
            <div className="m-b-20">
              <StyledButton className="full-width" type="primary" onClick={handleNext}>
                {t(`common.button.${screen >=4 ? 'start' : 'next'}`)}
              </StyledButton>
            </div>
            <div className="flex-container">
              <div className="flex-1 m-r-30">
                {screen > 1 && <StyledButton className="full-width" onClick={handlePrev}>
                  {t('common.button.prev')}
                </StyledButton>}
              </div>
              <div className="flex-1 m-l-30">
                {screen < 4 && <StyledButton className="full-width" onClick={handleNext}>
                  {t('common.button.skip')}
                </StyledButton>}
              </div>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className={`flex-container col flex-1 animated fade ${loading ? '' : 'fade-in-out'}`}>
      <div className={`animated fade ${page === 0 ? 'fade-in-out' : ''}`}>
        <Header title={t(`onBoarding.0.header`)} />
      </div>
      <div className="flex-1 flex-center">
        <Swiper
          slidesPerView={1}
          allowSlidePrev={page > 1}
          allowSlideNext={agree}
          onSwiper={sp => setSwiper(sp)}
          onSlideChange={sp => setPage(sp.activeIndex)}
        >
          {Array.from(Array(5), (e, i) => (
            <SwiperSlide
              key={`onBoarding_slide_${i}`}
              className={`flex-center ${i > 0 ? 'p-h-30' : 'p-h-20'}`}
            >
              {renderContent(i)}
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
};

export default OnBoarding;
