import React, { Fragment, useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import Draggable from 'react-draggable';
import CountUp from 'react-countup';
import { Modal } from 'antd';
import moment from 'moment';

import useWindowSize from './../../Hooks/useWindowSize';
import { StyledButton } from './../../Components/Styled';

const Dragger = props => {
  const { record, isDrag, setIsDrag, onLeave } = props;

  const { t } = useTranslation();
  const { width } = useWindowSize();

  const [type, setType] = useState(null);
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    document.togglePopup = (type = 'leave') => {
      if (!visible) setType(type);
      setVisible(v => !v);
    }
  }, []);

  const handleBeforeLeave = () => {
    if (!isDrag) {
      setType('leave');
      setVisible(true);
    }
  };

  const handleLeave = () => {
    onLeave();
    setVisible(false);
  };

  const renderCountUp = time => {
    const sec_num = parseInt(time, 10);
    let hours = Math.floor(sec_num / 3600);
    let minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    let seconds = sec_num - (hours * 3600) - (minutes * 60);
    if (hours < 10) hours = `0${hours}`;
    if (minutes < 10) minutes = `0${minutes}`;
    if (seconds < 10) seconds = `0${seconds}`;
    return hours === '00' ? `${minutes}:${seconds}` : `${hours}:${minutes}:${seconds}`;
  };

  const renderContent = () => {
    switch (type) {
      case 'leave':
        return (
          <Fragment>
            <div className="m-t-20 text-center">
              <h1 className="desc-color body-text m-b-5">
                {t(`${record?.type}.modal.title`)}
              </h1>
              {record?.type !== 'stay' && <h2 className="desc-color header-text m-b-5">
                {t(`${record?.type}.modal.subTitle`)}
              </h2>}
              <h3 className="body-color bold large-text m-b-5 letter-spacing-2">
                {record?.location || ''}
              </h3>
              <h4 className="desc-color body-text m-b-30">
                {record?.startedAt || ''}
              </h4>
            </div>
            <div className="m-v-50">
              <h3 className="body-color title-text bold text-center">
                {t('common.modal.popup.leave-question')}
              </h3>
            </div>
            {record?.leftAt && <div className="text-center m-b-10">
              <h4 className="body-color body-text normal">
                {t('common.modal.popup.auto', { time: moment(record?.leftAt).format('MM-DD HH:mm') })}
              </h4>
            </div>}
            <StyledButton
              type="primary"
              className="full-width"
              onClick={handleLeave}
            >
              {t('common.button.leave-now')}
            </StyledButton>
            <div className="m-t-20" />
            <StyledButton
              type="secondary"
              className="full-width"
              onClick={handleLeave}
            >
              {t('common.button.left')}
            </StyledButton>
          </Fragment>
        );
      case 'change':
        return (
          <Fragment>
            <div className="m-v-40">
              <h3 className="body-color title-text bold text-center">
                {t('common.modal.popup.others')}
              </h3>
            </div>
            <StyledButton
              type="primary"
              className="full-width"
              onClick={handleLeave}
            >
              {t('common.button.yes')}
            </StyledButton>
            <div className="m-t-20" />
            <StyledButton
              type="secondary"
              className="full-width"
              onClick={() => setVisible(false)}
            >
              {t('common.button.cancel')}
            </StyledButton>
          </Fragment>
        );
      default:
        return null;
    }
  };

  return (
    <Fragment>
      <Draggable
        bounds="body"
        onDrag={() => setIsDrag(true)}
        onStop={() => {
          handleBeforeLeave();
          setTimeout(() => setIsDrag(false), 0, setIsDrag)
        }}
      >
        <div
          className="dragger absolute bottom-110 center card-shadow text-center p-a-10 pointer"
          style={{
            left: (width / 2) - 60,
            display: record ? 'block' : 'none'
          }}
        >
          <img className="" alt="leave" src={require('./../../Assets/images/leave.svg')} />
          <div className="icon-text body-color normal">
            {t('common.button.leave')}
          </div>
          <div className="icon-text body-color bold">
            {record && <CountUp
              duration={99 * 60 * 60 * 8}
              start={0}
              end={99 * 60 * 60}
              formattingFn={renderCountUp}
            />}
          </div>
        </div>
      </Draggable>
      <Modal
        centered
        visible={visible}
        footer={null}
        wrapClassName="popup-modal"
        closeIcon={(<span className="title-text bold body-color"></span>)}
        onCancel={() => setVisible(false)}
      >
        {renderContent()}
      </Modal>
    </Fragment>
  );
};

export default Dragger;