import moment from 'moment';
import { SET_BASIC_REDUCER } from './../type';

export const loadLocalStorageBasicAction = () => {
  let { onBoarded, updatedAt } = localStorage;
  onBoarded = Boolean(onBoarded);
  if (!updatedAt) {
    updatedAt = updatedAt || moment().format('YYYY-MM-DD HH:mm:ss')
    localStorage.setItem('updatedAt', updatedAt);
  }

  return ({
    target: '',
    type: SET_BASIC_REDUCER,
    data: {
      onBoarded,
      updatedAt,
      infectionCount: 0
    }
  });
};

export const updateAction = () => {
  const updatedAt = moment().format('YYYY-MM-DD HH:mm:ss')
  localStorage.setItem('updatedAt', updatedAt);

  return ({
    target: 'updatedAt',
    type: SET_BASIC_REDUCER,
    data: updatedAt
  });
};

