import styled from 'styled-components';
import { Checkbox, Button } from 'antd';
import { COLORS } from './../../Utils/constant';

const {
  transparent,
  primaryColor,
  secondaryColor,
  disableColor,
  bodyColor,
  descColor,
  blankColor
} = COLORS;

export const StyledCircleChecked = styled.div`
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 4px ${blankColor} solid;
  position: relative;

  :after {
    content: ' ';
    position: absolute;
    border-left: 4px ${blankColor} solid;
    border-bottom: 4px ${blankColor} solid;
    height: 25px;
    width: 50px;
    top: 40px;
    left: 30px;
    transform: rotate(-45deg);
  }
`;

export const StyledCheckbox = styled(Checkbox)`
  .ant-checkbox .ant-checkbox-inner {
    width: 30px;
    height: 30px;
    ${props => props.theme === 'ghost' ?
      `border: 2px solid ${blankColor}; background-color: ${transparent};` :
      `border: 2px solid ${primaryColor};`
    }
  }

  .ant-checkbox-wrapper:hover .ant-checkbox-inner,
  .ant-checkbox:hover .ant-checkbox-inner,
  .ant-checkbox-input:focus + .ant-checkbox-inner {
    ${props => props.theme === 'ghost' ?
      `border-color: ${blankColor};` :
      `border-color: ${primaryColor};`
    }
  }

  .ant-checkbox-checked .ant-checkbox-inner {
    background-color: ${transparent};
    ${props => props.theme === 'ghost' ?
      `border-color: ${blankColor};` :
      `border-color: ${primaryColor};`
    }
  }

  .ant-checkbox-checked .ant-checkbox-inner::after {
    ${props => props.theme === 'ghost' ?
      `border: 1.9px solid ${blankColor};` :
      `border: 1.9px solid ${primaryColor};`
    }
    border-top: 0;
    border-left: 0;
    margin-left: 2px;
    margin-top: 2px;
    -webkit-transform: rotate(45deg) scale(1.9) translate(-50%, -50%);
    transform: rotate(45deg) scale(1.9) translate(-50%, -50%);
  }

  .ant-checkbox-checked::after {
    ${props => props.theme === 'ghost' ?
      `border: 1px solid ${blankColor};` :
      `border: 1px solid ${primaryColor};`
    }
  }


  &.ant-checkbox-wrapper-disabled {
    .ant-checkbox-checked .ant-checkbox-inner::after {
      border: 1.9px solid ${disableColor};
      border-top: 0;
      border-left: 0;
    }
  }
`;

export const StyledButton = styled(Button)`
  &.ant-btn {
    height: 50px;
    padding: 10px 15px;
    border-radius: 25px;
    font-weight: bold;
    color: ${bodyColor};
    border-color: ${transparent};
    box-shadow: none;
  }

  &.ant-btn.ant-btn-primary {
    background: ${secondaryColor};
    border-color: ${secondaryColor};
  }

  &.ant-btn.ant-btn-secondary {
    background: ${primaryColor};
    border-color: ${primaryColor};
    color: ${blankColor};
  }

  &.ant-btn:active,
  &.ant-btn.active {
    color: ${transparent};
    background-color: ${transparent};
    border-color: ${transparent};
  }

  &[ant-click-animating-without-extra-node='true']::after,
  &.ant-click-animating-node {
    border: 0 none;
    opacity: 0;
    animation:none 0 ease 0 1 normal;
  }

  &.ant-btn > span {
    letter-spacing: 1px;
  }

  &.ant-btn[disabled],
  &.ant-btn[disabled]:hover,
  &.ant-btn[disabled]:focus,
  &.ant-btn[disabled]:active {
      color: ${descColor};
      background: ${transparent};
      border-color: ${transparent};
      text-shadow: none;
      -webkit-box-shadow: none;
      box-shadow: none;
      opacity: 0.5;
  }

  &.ant-btn.ant-btn-secondary[disabled],
  &.ant-btn.ant-btn-secondary[disabled]:hover,
  &.ant-btn.ant-btn-secondary[disabled]:focus,
  &.ant-btn.ant-btn-secondary[disabled]:active,
  &.ant-btn.ant-btn-primary[disabled],
  &.ant-btn.ant-btn-primary[disabled]:hover,
  &.ant-btn.ant-btn-primary[disabled]:focus,
  &.ant-btn.ant-btn-primary[disabled]:active {
      color: ${descColor};
      background: ${disableColor};
      border-color: ${disableColor};
      text-shadow: none;
      -webkit-box-shadow: none;
      box-shadow: none;
      opacity: 1;
  }
`