import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Slider from 'react-slick';
import { find } from 'lodash';
import moment from 'moment';
import 'moment/locale/zh-hk'

import './style.scss';

const Home = () => {
  const { t } = useTranslation();
  const history = useHistory();
  moment.locale(t('language'));

  const records = useSelector(state => state?.records || []);
  const updatedAt = useSelector(state => state?.basic?.updatedAt || '');
  const infectionCount = useSelector(state => state?.basic?.infectionCount || 0);

  const record = find(records, { done: false });

  const handleActions = (type) => {
    if (!record) {
      document.toggleDrawer(type);
    } else {
      document.togglePopup(type === 'report' ? 'leave': 'change');
    }
  };

  const renderImageBtns = () => {
    const sliderSettings = {
      speed: 300,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      centerMode: true,
      centerPadding: "30px",
    };

    const slideClasses = 'img-btn card-shadow border-radius-10 pointer'
    const slides = [
      {
        image: require(`./../../Assets/images/home/stay_${t('language')}.jpg`),
        desc: 'scan place qrcode',
        action: () => handleActions('stay')
      },
      {
        image: require(`./../../Assets/images/home/transport_${t('language')}.jpg`),
        desc: 'scan transport qrcode',
        action: () => handleActions('transport')
      },
      {
        image: require(`./../../Assets/images/home/report_${t('language')}.jpg`),
        desc: 'report health',
        action: () => handleActions('report')
      }
    ];

    return (
      <Slider className="home-slider" {...sliderSettings}>
        {slides.map(slide => (
          <div key={slide.desc}>
            <img
              className={slideClasses}
              src={slide.image}
              alt={slide.desc}
              onClick={slide.action}
            />
          </div>
        ))}
      </Slider>
    );
  };

  const renderMessageCard = () => {
    return (
      <div className="m-t-20 p-h-15 text-center">
        <div
          className="case-img card-shadow border-radius-10 full-width flex-container flex-center pointer"
          style={{
            background: `url(${require('./../../Assets/images/home/case.jpg')}) center right no-repeat`,
            backgroundSize: 'cover'
          }}
          onClick={() => history.replace('messages')}
        >
          <div className="flex-1 text-left p-v-20 p-h-20 body-color">
            <div className="body-text">
              {t('home.notice.latest', { date: moment().format('YYYY-MM-DD') })}
            </div>
            <div className="header-text bold">
              {t('home.notice.count', { count: infectionCount })}
            </div>
            <div className="body-text">
              {t('home.notice.more')}
            </div>
          </div>
        </div>
      </div>
    )
  };

  return (
    <div className="home flex-1 flex-container col primary-background">
      <div className="upper-block p-a-20 full-width flex-container flex-center">
        <div className="flex-1 p-b-20">
          <h2 className="body-text normal blank-color text-shadow m-b-20">
            {moment(updatedAt).format('YYYY-MM-DD, dddd')}
          </h2>
          <h2 className="large-text bold blank-color text-shadow">
            {t('home.title')}
          </h2>
        </div>
      </div>
      <div className="lower-block flex-1 blank-background">
        {renderImageBtns()}
        {renderMessageCard()}
      </div>
    </div>
  );
};

export default Home;
