import React from 'react';

const Header = (props) => {
  const { title, left } = props;
  
  if (title) {
    return (
      <div className="header primary-background flex-container">
        <div className="text-center flex-1 flex-center relative">
          {left && <div className="absolute p-l-15">
            {left}
          </div>}
          <h1 className="header-text blank-color text-shadow">
            {title}
          </h1>
        </div>
      </div>
    );
  }
  return null;
};

export default Header;