import React, { Fragment, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { Modal } from 'antd';
import { CheckOutlined } from '@ant-design/icons';
import { find } from 'lodash';
import moment from 'moment';

import {
  StyledCircleChecked,
  StyledCheckbox,
  StyledButton
} from './../../Components/Styled';
import ListItem from './../ListItem/index';
import { updateRecordAction } from './../../Reducers/Records/actions';
import './style.scss';

const TrackModal = (props) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const { onCancel, ...rest } = props;

  const records = useSelector(state => state?.records);
  const record = find(records, { leftAt: null, done: false });

  const [hours, setHours] = useState(1);
  const [auto, setAuto] = useState(false);
  const [visible, setVisible]  = useState(false);

  const handleCancel = () => {
    if (auto) {
      dispatch(updateRecordAction(hours, false));
    }
    onCancel();    
  };

  const handleSubmit = () => {
    document.togglePopup();
  };

  const renderContent = () => {
    if (!record?.type) return null;
    return (
      <Fragment>
        <div className="app-modal-content text-shadow text-center flex-container">
          <div className="flex-1 flex-center">
            <h1 className="blank-color header-text m-b-5">
              {t(`${record.type}.modal.title`)}
            </h1>
            {record.type !== 'stay' && <h2 className="blank-color header-text m-b-5">
              {t(`${record.type}.modal.subTitle`)}
            </h2>}
            <h3 className="secondary-color large-text m-b-5 letter-spacing-2">
              {record?.location || ''}
            </h3>
            <h4 className="blank-color header-text m-b-30">
              {record?.startedAt || ''}
            </h4>
            <div className="m-b-60">
              <StyledCircleChecked className="center" />
            </div>
            <div className="m-b-20 flex-container flex-center">
              <div className="flex-auto m-r-10">
                <StyledCheckbox
                  theme="ghost"
                  checked={auto}
                  onChange={(e) => setAuto(e.target.checked)}
                />
              </div>
              <div className="flex-1 flex-center text-left body-text blank-color">
                {t('common.modal.track.leave', { hours })}
              </div>
              <div
                className="flex-auto flex-center text-left body-text blank-color pointer"
                onClick={() => setVisible(true)}
              >
                {t('common.button.change')}
              </div>
            </div>
            <div className="p-h-20">
              <StyledButton
                type="primary"
                className="full-width"
                onClick={handleSubmit}
              >
                {t('common.button.leave')}
              </StyledButton>
              <h4 className="blank-color body-text normal m-t-20">
                {t('common.modal.track.reminder')}
              </h4>
            </div>
          </div>
        </div>
        <Modal
          centered
          visible={visible}
          footer={null}
          wrapClassName="popup-modal"
          closeIcon={(<span className="title-text bold body-color"></span>)}
          onCancel={() => setVisible(false)}
        >
          <div className="m-t-20 text-center">
            <h1 className="body-color title-text bold m-b-10">
              {t('common.modal.popup.title')}
            </h1>
            <div className="select-list">
              {Array.from(Array(24), (e, i) => (
                <ListItem
                  key={`hours_${i + 1}`}
                  title={t('common.modal.popup.hours', { hours: i + 1 })}
                  boxClasses={`m-a-5 ${i === 0 ? 'p-b-15' : 'p-v-15'}`}
                  textClasses="normal"
                  icon={hours === i + 1 ? <CheckOutlined className="primary-color" style={{ fontSize: 20 }} /> : <span />}
                  action={() => setHours(i + 1)}
                />
              ))}
            </div>
          </div>
          <div className="text-center m-v-20">
            <h4 className="body-color body-text normal m-b-10">
              {t('common.modal.popup.start', { time: moment(record.startedAt).format('MM-DD HH:mm') })}
            </h4>
            <h4 className="body-color body-text normal m-b-10">
              {t('common.modal.popup.leave', { time: moment(record.startedAt).add(hours, 'hours').format('MM-DD HH:mm') })}
            </h4>
          </div>
          <StyledButton
            type="primary"
            className="full-width"
            onClick={() => setVisible(false)}
          >
            {t('common.button.confirm')}
          </StyledButton>
        </Modal>
      </Fragment>
    );
  };

  return (
    <Modal {...rest} onCancel={handleCancel}>
      {renderContent()}
    </Modal>
  )
};

export default TrackModal