import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory, useLocation } from 'react-router-dom';
import {
  HomeFilled,
  MailFilled,
  SettingFilled,
  HomeOutlined,
  MailOutlined,
  SettingOutlined
} from '@ant-design/icons';

const BottomTab = () => {
  const { t } = useTranslation();
  const history = useHistory();
  const location = useLocation();
  const { pathname } = location;

  const tabs = [
    {
      path: '/home',
      text: 'bottom-tabs.home',
      inactive: <HomeOutlined className="icon-text desc-color" />,
      active: <HomeFilled className="icon-text primary-color text-shadow" />
    },
    {
      path: '/messages',
      text: 'bottom-tabs.messages',
      inactive: <MailOutlined className="icon-text desc-color" />,
      active: <MailFilled className="icon-text primary-color text-shadow" />
    },
    {
      path: '/settings',
      text: 'bottom-tabs.settings',
      inactive: <SettingOutlined className="icon-text desc-color" />,
      active: <SettingFilled className="icon-text primary-color text-shadow" />
    }
  ];

  const renderTabs = () => {
    return tabs.map(tab => (
      <div key={tab.path} className="flex-1 ponter" onClick={() => history.replace(tab.path)}>
        <div className="text-center">
          {tab.path === pathname ? tab.active : tab.inactive}
        </div>
        <div className={`text-center m-t-5 ${tab.path === pathname ? 'primary-color' : 'desc-color'}`}>
          {t(tab.text)}
        </div>
      </div>
    ))
  };

  return (
    <div className="bottom-tab flex-container p-v-20 p-h-10">
      {renderTabs()}
    </div>
  );
};

export default BottomTab;